import ReviewService from '@/services/ReviewService.js'

const reviewStore = {
    state: {
        reviews: [],
        newReview: {
            toyId: '', // From toy store
            userId: '', // From session
            content: '', // Empty
        },
    },
    mutations: {
        loadReviews(state, { reviews }) {
            state.reviews = reviews;
        },
        addReview(state, {review}) {
            state.reviews.unshift(review);
        },
        clearReviews(state) {
            reviews = [];
        }
    },
    getters: {
        reviews(state) {
            return state.reviews;
        },
        newReview(state, getters) {
            return {
                toyId: getters.toyToDisplay._id,
                content: '' 
            }
        }
    },
    actions: {
        loadReviews(context, { toyId = null, userId = null }) {
            return ReviewService.query(toyId, userId)
                .then(reviews => {
                    context.commit({ type: 'loadReviews', reviews })
                })
        },
        saveReview(context , {newReview}) {
            return ReviewService.add(newReview)
                .then(review => {
                    context.commit({type: 'addReview', review});
                })
        }
    }
}

export default reviewStore;