<template>
  <section class="toy-app">
    <button @click="logout">Logout</button>
    <router-view @remove-toy="removeToy" :toys="toys" :isAdmin="(loggedUser)? !!loggedUser.isAdmin : false"/>
  </section>
</template>

<script>
import ToyList from '@/components/ToyList.vue';

export default {
  name: 'ToyApp',
  components: {
    ToyList
  },
  methods: {
    removeToy(toyId) {
      this.$store.dispatch({ type: 'removeToy', toyId });
    },
    logout() {
      this.$store.dispatch('logout')
        .then( () => {
          this.$router.push('/');
        })
    },
  },
  created() {
    this.$store.dispatch('loadToys')
      .then(() => {
        this.$store.commit('setToyTerms')
        });
    if (!this.loggedUser) this.$router.push('/');
  },
  computed: {
    toys() {
      return this.$store.getters.toys;
    },
    loggedUser() {
      return this.$store.getters.loggedUser;
    },
  }
};
</script>