<template>
  <section class="toy-edit">
    <h2>{{(toyToEdit && toyToEdit._id)? 'Edit Toy:' : 'Add a new Toy:'}}</h2>
    <form @submit.prevent="save" v-if="toyToEdit" class="flex column align-center justify-center">
      <label>
        Title:&nbsp;
        <input v-model="toyToEdit.title">
      </label>
      <label>
        Price:&nbsp;
        <input v-model.number="toyToEdit.price">
      </label>
      <label>
        Type:&nbsp;
        <input v-model="toyToEdit.type">
      </label>
      <label>
        Available in stock?&nbsp;
        <input class="checkbox-instock" type="checkbox" :checked="!toyToEdit.inStock" v-model="toyToEdit.inStock">
      </label>
      <button type="submit">Save</button>
    </form>
  </section>
</template>

<script>
export default {
  name: "ToyEdit",
  created() {
    if (!this.loggedUser || !this.loggedUser.isAdmin) this.$router.push('/');
    const { toyId } = this.$route.params;
    if (toyId) this.$store.dispatch({ type: "getToyById", toyId });
  },
  destroyed() {
    this.$store.commit("clearSelectedToy");
  },
  methods: {
    save() {
      this.$store
        .dispatch({ type: "saveToy", toy: this.toyToEdit })
        .then(() => {
          this.$router.push("/toys");
        });
    }
  },
  computed: {
    toyToEdit() {
      return this.$store.getters.toyToEdit;
    },
    loggedUser() {
      return this.$store.getters.loggedUser;
    },
  }
};
</script>
