<template>
  <form @submit.prevent="filterToys" class="toy-filter">
    <div class="searchbar-container">
      <button>
        <i class="fas fa-search"></i>
      </button>
      <input
        type="search"
        @input="generateSuggestedTerms"
        list="toySearchTerms"
        v-model="filterBy.term"
        placeholder="Search for a toy"
      >
      <button @click.prevent.stop="clearSearch">
        <i class="fas fa-times"></i>
      </button>
      <datalist id="toySearchTerms">
        <option :value="term" v-for="term in suggestedTerms" :key="term"/>
      </datalist>
      <select @change="filterToys" v-model="filterBy.inStock">
        <option :value="''">All</option>
        <option :value="true">In Stock</option>
        <option :value="false">Sold Out</option>
      </select>
    </div>
    <div class="sort-container">
      <select @change="filterBy.order = 1; filterToys()" v-model="filterBy.sortBy">
        <option value="title">Title</option>
        <option value="price">Price</option>
      </select>
      <label>
        Order:
        <button @click="filterBy.order *= -1">â</button>
      </label>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      filteredTerms: []
    };
  },
  methods: {
    filterToys() {
      this.$store.dispatch({ type: "loadToys", filterBy: this.filterBy });
    },
    generateSuggestedTerms() {
      this.filteredTerms = this.toyTerms.filter(
        term => term.toLowerCase().includes(this.filterBy.term.toLowerCase()),
        []
      );
    },
    clearSearch() {
      this.filterBy.term = '';
      this.filterToys();
    }
  },
  computed: {
    filterBy() {
      return this.$store.getters.filterToysBy;
    },
    toyTerms() {
      return this.$store.getters.toyTerms;
    },
    suggestedTerms() {
      return this.filteredTerms;
    }
  }
};
</script>
