<template>
    <section>
        <h1>User:</h1>
        <pre>{{user}}</pre>
        <h2>Reviews:</h2>
        <pre>{{userReviews}}</pre>
    </section>
</template>

<script>
export default {
    created() {
        console.log('Hi');
        const {userId} = this.$route.params;
        console.log(userId);
        if (userId) this.$store.dispatch({type: 'loadReviews', userId});
        
    },
    destroyed() {
        this.$store.commit('clearSelectedUser');
    },
    computed: {
        userReviews() {
            return this.$store.getters.reviews;
        },
        user() {
            return this.$store.getters.userToDisplay;
        }
    }
}
</script>

