<template>
  <router-link
    :title="`See ${toy.title}`"
    tag="li"
    :to="'/toys/display/' + toy._id"
    class="toy-preview"
  >
    <img class="img-toy" :src="loadThumbnail" :alt="`${toy.title}'s Logo'`">
    <h1>{{toy.title}}</h1>
    <p>${{toy.price}}</p>
    <p class="toy-stock-badge" :class="{'in-stock': toy.inStock}">{{isInStock}}</p>
    <div class="btns-container" v-show="isAdmin">
      <router-link @click.stop tag="button" :to="'/toys/edit/' + toy._id">
        <i class="fas fa-edit"></i>
      </router-link>
      <button @click.stop="$emit('remove-toy', toy._id)">
        <i class="fas fa-times"></i>
      </button>
    </div>
  </router-link>
</template>

<script>
export default {
  name: "ToyPreview",
  props: {
    toy: Object,
    isAdmin: Boolean,
  },
  computed: {
    isInStock() {
      return this.toy.inStock ? "In Stock" : "Sold Out";
    },
    loadThumbnail() {
      return `https://api.adorable.io/avatars/120/${
        this.toy.title
      }@adorable.pngCopy`;
    }
  }
};
</script>

