import Axios from 'axios'
var axios = Axios.create({
    withCredentials: true
})

export default {
    query,
    add
}

const REVIEW_API = (process.env.NODE_ENV !== 'development')
    ? '/review'
    : '//localhost:3003/review';

function query(toyId, userId) {
    let query = '?';
    if (userId) query += `userId=${userId}&`;
    if (toyId) query += `toyId=${toyId}`;
    return axios.get(`${REVIEW_API}${query}`)
        .then(res => res.data);
}

function add(newReview) {
    return axios.post(REVIEW_API, newReview)
        .then(res => res.data);
}