<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png">
    <form @submit.prevent="login">
      <div>{{response}}</div>
      <input v-model="credentials.username" placeholder="username">
      <input v-model="credentials.password" placeholder="password">
      <button>Login</button>
    </form>
    <router-link to="/signup" tag="button">Sign up</router-link>
    {{loggedUser}}
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      credentials: {
        username: "",
        password: ""
      },
      response: null
    };
  },
  created() {
    if (this.loggedUser) {
      this.$router.push("/toys");
    }
  },
  computed: {
    loggedUser() {
      return this.$store.getters.loggedUser;
    }
  },
  methods: {
    login() {
      this.$store
        .dispatch({ type: "login", credentials: this.credentials })
        .then(() => {
          this.$router.push("/toys");
        })
        .catch(err => (this.response = err));
    }
  }
};
</script>
