<template>
  <section class="toy-details" v-if="toy">
    <article class="details-container">
      <h1>{{toy.title}}</h1>
      <h3>{{toy.type}}</h3>
      <p>{{toy.price}}</p>
      <p class="toy-stock-badge" :class="{'in-stock': toy.inStock}">{{isInStock}}</p>
    </article>
    <review-add />
    <review-list :toyId="toy._id" />
  </section>
</template>

<script>
import ReviewAdd from "@/components/ReviewAdd.vue";
import ReviewList from "@/components/ReviewList.vue";

export default {
  name: "ToyDetails",
  components: { ReviewList, ReviewAdd },
  created() {
    if (!this.loggedUser) this.$router.push("/");

    const { toyId } = this.$route.params;
    if (toyId) this.$store.dispatch({ type: "getToyById", toyId });
  },
  destroyed() {
    this.$store.commit("clearSelectedToy");
  },
  methods: {},
  computed: {
    toy() {
      return this.$store.getters.toyToDisplay;
    },
    loggedUser() {
      return this.$store.getters.loggedUser;
    },
    isInStock() {
      return this.toy.inStock ? "In Stock" : "Sold Out";
    }
  }
};
</script>
