import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/Home.vue';
import SignUp from '@/views/SignUp.vue';

// Toys App
import Toys from '@/views/Toys.vue';
import ToyList from '@/components/ToyList.vue';
import ToyEdit from '@/views/ToyEdit.vue';
import ToyDetails from '@/views/ToyDetails.vue';

// Users
import UserDetails from '@/views/UserDetails.vue';

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', component: Home },
    { path: '/signup', component: SignUp },
    {
      path: '/toys',
      component: Toys,
      children: [
        { path: '', component: ToyList },
        { path: 'display/:toyId', component: ToyDetails },
        { path: 'edit/:toyId?', component: ToyEdit },
        { path: 'user/:userId', component: UserDetails },
      ]
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
