import UserService from '@/services/UserService.js'

const userStore = {
    state: {
        loggedUser: UserService.autoLogin(),
        emptyUser: UserService.getEmpty(),
        userToDisplay: null,
    },
    mutations: {
        setUser(state, { user }) {
            state.loggedUser = user;
        },
        clearSelectedUser(state) {
            state.userToDisplay = null;
        }
    },
    getters: {
        loggedUser(state) {
            return state.loggedUser;
        },
        emptyUser(state) {
            return JSON.parse(JSON.stringify(state.emptyUser));
        },
        userToDisplay(state) {
            return state.userToDisplay;
        }
    },
    actions: {
        login(context, { credentials }) {
            return UserService.login(credentials)
                .then(user => {
                    context.commit({ type: 'setUser', user });
                })
                .catch(err => { throw err });
        },
        signup(context, { newUser }) {
            return UserService.signup(newUser)
                .then(newUser => {
                    context.commit({ type: 'setUser', user: newUser });
                })
                .catch(err => {throw err});
        },
        logout(context) {
            return UserService.logout()
                .then( () => {
                    context.commit({type: 'setUser', user: null});
                })
        }
    }
}

export default userStore;