import Axios from 'axios'
var axios = Axios.create({
    withCredentials: true
})

export default {
    query,
    getById,
    getEmpty,
    add,
    update,
    remove
}



const TOY_API = (process.env.NODE_ENV !== 'development')
    ? '/toy'
    : '//localhost:3003/toy';

function query(filterBy) {
    // send proper query
    let query = '';
    if (filterBy) {
        query += '?'
        if (filterBy.term) query += `title=${filterBy.term}&type=${filterBy.term}&`;
        if (filterBy.price) query += `price=${filterBy.price}&`;
        if (filterBy.inStock === true || filterBy.inStock === false) query += `inStock=${filterBy.inStock}&`;
        if (filterBy.sortBy) query += `sortBy=${filterBy.sortBy}&order=${filterBy.order}`;
    }

    return axios.get(`${TOY_API}${query}`)
        .then(res => res.data);
}

function getById(id) {
    return axios.get(`${TOY_API}/${id}`)
        .then(res => res.data);
}

function getEmpty() {
    return {
        title: '',
        price: 0,
        createdAt: null,
        inStock: true,
        type: '',
    }
}

function add(toy) {
    toy.createdAt = Date.now();
    return axios.post(TOY_API, toy)
        .then(res => res.data);
}

function update(toy) {
    return axios.put(`${TOY_API}/${toy._id}`, toy).then(res => res.data);
}

function remove(toyId) {
    return axios.delete(`${TOY_API}/${toyId}`)
        .then(res => res.data);
}