<template>
  <article>
    <h1>Reviews:</h1>
    <div>toyId: {{toyId}}</div>
    <ul class="review-list">
      <li v-for="review in reviews" :key="review._id">
        <router-link tag="p" :to="'/user/' + review.userId">{{review.user.username}}</router-link>
        {{review}};
      </li>
    </ul>
  </article>
</template>

<script>
export default {
  props: {
    toyId: String
  },
  created() {
    this.$store.dispatch({ type: "loadReviews", toyId: this.toyId });
  },
  computed: {
    reviews() {
      return this.$store.getters.reviews;
    }
  }
};
</script>

