<template>
  <article>
    <button @click="isReviewing = !isReviewing">{{(isReviewing)? 'Discard Review' : 'Add Review' }}</button>
    <form @submit.prevent="save" v-if="isReviewing">
      <textarea v-model="newReview.content" placeholder="Write your review here"></textarea>
      <button>Save</button>
    </form>
  </article>
</template>

<script>
export default {
  data() {
    return {
      isReviewing: false
    };
  },
  methods: {
    save() {
      this.$store.dispatch({ type: "saveReview", newReview: this.newReview })
        .then(() => {
        this.isReviewing = false;
      });
    }
  },
  computed: {
    newReview() {
      return this.$store.getters.newReview;
    }
  }
};
</script>

