import ToyService from '@/services/ToyService.js'

const toyStore = {
    state: {
        toys: [],
        toyTerms: [],
        toyToDisplay: null,
        filterToysBy: {
            term: '',
            inStock: '',
            price: null,
            sortBy: 'title',
            order: 1,
        },
    },
    mutations: {
        loadToys(state, { toys }) {
            state.toys = toys;
        },
        setToyToDisplay(state, { toy }) {
            state.toyToDisplay = toy;
        },
        setToyTerms(state) {
            state.toyTerms = [...state.toys.reduce((acc, toy) => {
                acc.add(toy.title);
                acc.add(toy.type);
                return acc;
            }, new Set())];
        },
        clearSelectedToy(state) {
            state.toyToDisplay = null;
        },
        addToy(state, { newToy }) {
            state.toys.unshift(newToy)
        },
        updateToy(state, { toy }) {
            const idx = state.toys.findIndex(existingToy => existingToy._id === toy._id);
            state.toys.splice(idx, 1, toy);
        },
        removeToy(state, { toyId }) {
            const idx = state.toys.findIndex(toy => toy._id === toyId);
            state.toys.splice(idx, 1);
        },
    },
    getters: {
        toys(state) {
            return state.toys;
        },
        toyTerms(state) {
            return state.toyTerms;
        },
        toyToEdit(state) {
            return (state.toyToDisplay) ? JSON.parse(JSON.stringify(state.toyToDisplay)) : ToyService.getEmpty();
        },
        toyToDisplay(state) {
            return state.toyToDisplay;
        },
        filterToysBy(state) {
            return JSON.parse(JSON.stringify(state.filterToysBy));
        }
    },
    actions: {
        loadToys(context, payload) {
            var filterBy = (payload && payload.filterBy) ? payload.filterBy : null;
            return ToyService.query(filterBy)
                .then(toys => {
                    context.commit({ type: 'loadToys', toys });
                })
        },
        getToyById(context, { toyId }) {
            return ToyService.getById(toyId)
                .then(toy => {
                    context.commit({ type: 'setToyToDisplay', toy });
                })
        },
        saveToy(context, { toy }) {
            if (toy._id) return ToyService.update(toy)
                .then(() => {
                    context.commit({ type: 'updateToy', toy });
                    context.commit('clearSelectedToy');
                })
            else return ToyService.add(toy)
                .then(newToy => {
                    context.commit({ type: 'addToy', newToy })
                })
        },
        removeToy(context, { toyId }) {
            return ToyService.remove(toyId)
                .then(() => {
                    context.commit({ type: 'removeToy', toyId })
                })
        }
    }
}

export default toyStore;