<template>
  <section class="signup">
    <h1>Sign Up</h1>
    <form @submit.prevent="signup" class="flex column justify-center align-center">
      <div v-show="errMsg">{{errMsg}}</div>
      <input placeholder="Username" v-model="newUser.username" type="text" required />
      <input placeholder="Password" v-model="newUser.password" type="password" required />
      <input placeholder="Confirm Password" v-model="passwordConfirm" type="password" required />
      <input placeholder="First name. e.g. John" v-model="newUser.firstName" type="text" required />
      <input placeholder="Last name. e.g. Doe" v-model="newUser.lastName" type="text" required />
      <button type="submit">Sign up</button>
    </form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      errMsg: null,
      passwordConfirm: '',
    };
  },
  methods: {
    signup() {
      if (this.newUser.password !== this.passwordConfirm) {
        this.errMsg = 'Password confirmation failed. Please re-type it.';
        setTimeout( () => {this.errMsg = ''}, 2500);
        return;
      } else {
          this.$store.dispatch({type: 'signup', newUser: this.newUser})
          .then(() => {
              this.$router.push('/toys');
          })
          .catch(err => {
              this.errMsg = err;
              setTimeout( () => {this.errMsg = ''}, 2500);
          })
      }
    }
  },
  computed: {
      newUser() {
          return this.$store.getters.emptyUser;
      }
  }
};
</script>

