import Axios from 'axios'
var axios = Axios.create({
    withCredentials: true
})

import StorageService from './StorageService.js';

export default {
    query,
    login,
    signup,
    autoLogin,
    logout,
    getById,
    getEmpty,
    update,
    remove
}

const USER_KEY = 'loggedUser';

const USER_API = (process.env.NODE_ENV !== 'development')
    ? '/user'
    : '//localhost:3003/user';

const LOGIN = (process.env.NODE_ENV !== 'development')
    ? '/login'
    : '//localhost:3003/login';

const SIGNUP = (process.env.NODE_ENV !== 'development')
    ? '/signup'
    : '//localhost:3003/signup';

const LOGOUT = (process.env.NODE_ENV !== 'development')
    ? '/logout'
    : '//localhost:3003/logout';

function query(filterBy) {
    // send proper query
    let query = '';

    return axios.get(`${USER_API}${query}`)
        .then(res => res.data);
}

function login(credentials) {
    return axios.post(LOGIN, credentials)
        .then(res => res.data)
        .then(user => {
            StorageService.saveToLocal(USER_KEY, user);
            return user;
        })
        .catch(err => { throw ('Wrong Username and/or Password.') });
}

function signup(newUser) {
    newUser.createdAt = Date.now();
    return axios.post(SIGNUP, newUser)
        .then(res => res.data)
        .catch(err => { throw ('Username is already taken. Please choose another one.') });
}

function autoLogin() {
    return StorageService.loadFromLocal(USER_KEY)
}

function logout() {
    return axios.post(LOGOUT)
        .then(res => {
            StorageService.removeFromLocal(USER_KEY);
            return;
        })
}

function getById(id) {
    return axios.get(`${USER_API}/${id}`)
        .then(res => res.data);
}

function getEmpty() {
    return {
        username: '',
        password: '',
        firstName: '',
        lastName: '',
        isAdmin: false,
        createdAt: null,
    }
}



function update(user) {
    return axios.put(`${USER_API}/${user._id}`, user).then(res => res.data);
}

function remove(userId) {
    return axios.delete(`${USER_API}/${userId}`)
        .then(res => res.data);
}